import { useState, useEffect, useRef } from "react";
import axios from "axios";
import "./page.css";
import { useNavigate } from "react-router-dom";
import JoditEditor from "jodit-react";
import { current } from "@reduxjs/toolkit";

// const baseurl = "https://blog-back-7jx6.onrender.com";
// const baseurl = "http://localhost:4000";
const baseurl = `https://backblog.kusheldigi.com`

function CreatePage() {
  const editorRef = useRef(null);
  const [formData, setFormData] = useState({
    title: "",
    images: [],
    banner: [],
    categoryId: "",
    subdescription: "",
    author: "",
    designation: "",
    domain: "",
    time: "",
  });
  const [categories, setCategories] = useState([]);
  const navigate = useNavigate();

  const [content, setContent] = useState("");

  const editor = useRef(null);


  useEffect(() => {
    console.log('This is Create Page')
    const fetchCategories = async () => {
      try {
        const response = await axios.get(`${baseurl}/api/v1/auth/categories`);
        setCategories(response.data.categories || []);
      } catch (error) {
        console.error("Error fetching categories:", error);
      }
    };
    fetchCategories();
  }, []);

  // Handle input changes
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  // Handle file input changes
  const handleFileChange = (e) => {
    const files = Array.from(e.target.files);
    setFormData({ ...formData, images: files });
  };
  const handleFileChange2 = (e) => {
    const files = Array.from(e.target.files);
    setFormData({ ...formData, banner: files });
  };

  // Handle form submission
  const handleSubmit = async (e) => {
    console.log(formData)
    e.preventDefault();
    const currentTime = new Date().toLocaleTimeString("en-US", {
      hour: "2-digit",
      minute: "2-digit",
      hour12: false,
    });

    const data = new FormData();
    data.append("title", formData.title);
    data.append("description", content);
    data.append("subdescription", formData.subdescription);
    data.append("categoryId", formData.categoryId);
    data.append("domain", formData.domain);
    data.append("author", formData.author);
    data.append("designation", formData.designation);
    data.append("time", formData.time);
    formData.images.forEach((image) => data.append("images", image));
    formData.banner.forEach((image) => data.append("banner", image));

    try {
      const response = await axios.post(`${baseurl}/api/v1/auth/createBlog`, data, {
        headers: { "Content-Type": "multipart/form-data" },
      });

      if (response.data.status) {
        alert("Blog created successfully!");
        setFormData({
          title: "",
          description: "",
          images: [],
          banner: [],
          categoryId: "",
          domain: ""
        });
        navigate("/allBlog");
      }
    } catch (error) {
      console.error("Error creating blog:", error);
      alert("Failed to create blog");
    }
  };

  return (
    <section className="App">
      <h2>CREATE BLOG</h2>
      <form onSubmit={handleSubmit}>

        <label>
          <p>Title</p>
          <input
            type="text"
            name="title"
            value={formData.title}
            onChange={handleInputChange}
            required
          />
        </label>

        <label>
          <p>Sub Description</p>
          <input
            type="text"
            name="subdescription"
            value={formData.subdescription}
            onChange={handleInputChange}
            required
          />
        </label>

        <label>
          <p>Author Name</p>
          <input
            type="text"
            name="author"
            value={formData.author}
            onChange={handleInputChange}
            required
          />
        </label>

        <label>
          <p>Desigination</p>
          <input
            type="text"
            name="designation"
            value={formData.designation}
            onChange={handleInputChange}
            required
          />
        </label>

        <label>
          <p>Reading Time</p>
          <input
            type="time"
            name="time"
            value={formData.time}
            onChange={handleInputChange}
            required
            aria-label="Select reading time"
          />
        </label>

        <label>
          <p>Description</p>
          <JoditEditor
            ref={editor}
            value={content}
            tabIndex={1}
            onBlur={(newContent) => setContent(newContent)}
            onChange={(newContent) => {
              setContent(newContent);
            }}

          />
        </label>

        <label>
          <p>Category</p>
          <select
            name="categoryId"
            className="options"
            value={formData.categoryId}
            onChange={handleInputChange}
            required
          >
            <option value="">Select a category</option>
            {categories.map((category) => (
              <option key={category._id} value={category._id}>
                {category.title}
              </option>
            ))}
          </select>
        </label>

        <label>
          <p>Domain</p>
          <select name="domain" value={formData.domain} onChange={handleInputChange} required>
            {console.log(formData.domain)}
            <option value="">Select a category</option>
            <option value="kusheldigi.com" >kusheldigi.com</option>
            <option value="kusheldigi.us">kusheldigi.us</option>
          </select>
        </label>

        <label>
          <p>Image</p>
          <input type="file" multiple onChange={handleFileChange} />
        </label>

        <label>
          <p>Banner</p>
          <input type="file" multiple onChange={handleFileChange2} />
        </label>

        <button className="create-btn" type="submit">Create Blog</button>
        <button
          onClick={() => navigate("/allBlog")}
          className="dfewrew"
          type="button"
        >
          Go to all blog
        </button>
        <button onClick={() => navigate("/category")}>Go to create category page</button>
        <button onClick={() => navigate("/allCategory")}>All Category</button>
      </form>
    </section>
  );
}

export default CreatePage;
